import { useAtom } from "jotai";

import nicknameAtom from "shared/atoms/nicknameAtom";
import useDonationHistoryData from "./apis/useDonationHistoryData";
import useDonationHistory from "./hooks/useDonationHistory";

import DonationItem from "features/donationHistory/components/DonationItem/Container";
import Announcement from "shared/components/Announcement/Container";

import {
  AnnouncementContainer,
  Container,
  ContentContainer,
  DonationList,
  DonationTotal,
  LoadMoreButton,
  StyledDescription,
  StyledNavigationBar,
  StyledTitle,
  Subtitle,
  SubtitleArea,
} from "./Container.style";

const DonationHistoryContainer = () => {
  const [nickname] = useAtom(nicknameAtom);
  const { donationList, totalDonation, totalCount } = useDonationHistoryData();
  const { handleLoadMoreClick, handleNoticeClick } = useDonationHistory();

  return (
    <Container>
      <ContentContainer>
        <StyledNavigationBar title="  " />
        <StyledTitle
          text={
            nickname.length > 0
              ? `  !
        ${nickname}  :)`
              : `  !
         :)`
          }
        />
        <StyledDescription text="      !" />
        <SubtitleArea>
          <Subtitle>
            <span>{nickname}</span>
            {nickname.length > 0
              ? `
             `
              : ` `}
          </Subtitle>
          <DonationTotal>
            <p>  </p>
            <span>{`${totalDonation}`}</span>
          </DonationTotal>
        </SubtitleArea>
        <DonationList>
          {donationList.map((item, index) => (
            <DonationItem key={index} item={item} />
          ))}
        </DonationList>
        {totalCount > donationList.length && (
          <LoadMoreButton onClick={handleLoadMoreClick}></LoadMoreButton>
        )}
      </ContentContainer>
      <AnnouncementContainer>
        <Announcement
          buttonText=""
          leftText="  "
          onButtonClick={handleNoticeClick}
          rightText="   !"
        />
      </AnnouncementContainer>
    </Container>
  );
};

export default DonationHistoryContainer;
