import useFindAccount from "./hooks/useFindAccount";

import FindIdTabContents from "features/findAccount/components/FindAccountIdTabContents/Container";
import FindPasswordTabContents from "./components/FindPasswordTabContents/Container";

import {
  ContentContainer,
  StyledTitle,
  StyledDescription,
  TabArea,
  TabItem,
  TabContentsPaper,
  TabList,
} from "./Container.style";
import ScrollableContainer from "shared/components/ScrollableContainer/Container";

const FindAccountContainer = () => {
  const { handleJoinClick, handleTabClick, selectedTab } = useFindAccount();

  return (
    <ScrollableContainer
      bottomAnnouncement={{
        buttonText: " ",
        leftText: " ",
        onButtonClick: handleJoinClick,
        rightText: " !",
      }}
    >
      <ContentContainer>
        <StyledTitle
          text={`-  
         !`}
        />
        <StyledDescription text="    " />
        <TabArea>
          <TabList>
            <TabItem
              onClick={() => handleTabClick("Id")}
              selected={selectedTab === "Id"}
            >
               
            </TabItem>
            <TabItem
              onClick={() => handleTabClick("Password")}
              selected={selectedTab === "Password"}
            >
               
            </TabItem>
          </TabList>
          <TabContentsPaper>
            {selectedTab === "Id" ? (
              <FindIdTabContents />
            ) : (
              <FindPasswordTabContents />
            )}
          </TabContentsPaper>
        </TabArea>
      </ContentContainer>
    </ScrollableContainer>
  );
};

export default FindAccountContainer;
