import useLogin from "./hooks/useLogin";

import ScrollableContainer from "shared/components/ScrollableContainer/Container";
import Button, { ButtonTheme } from "shared/components/Button/Container";
import InputArea from "shared/components/InputArea/Container";
import Input from "shared/components/Input/Container";

import {
  ContentContainer,
  StyledTitle,
  StyledDescription,
  FindAccountAnnouncement,
} from "./Container.style";

const LoginContainer = () => {
  const {
    canSubmit,
    handleJoinClick,
    handleFindAccountClick,
    handleSubmitClick,
    handleAccountIdChange,
    accountId,
    accountIdWarningMessage,
    handlePasswordChange,
    password,
    passwordWarningMessage,
  } = useLogin();

  return (
    <ScrollableContainer
      bottomAnnouncement={{
        buttonText: " ",
        leftText: " ",
        onButtonClick: handleJoinClick,
        rightText: " !",
      }}
    >
      <ContentContainer>
        <StyledTitle
          text={`   :)
            !`}
        />
        <StyledDescription text="     " />
        <InputArea>
          <Input
            onValueChange={handleAccountIdChange}
            placeholder="  ?"
            title=""
            value={accountId}
            warningMessage={accountIdWarningMessage}
          />
          <Input
            onValueChange={handlePasswordChange}
            placeholder="  ?"
            title=""
            type="password"
            value={password}
            warningMessage={passwordWarningMessage}
          />
        </InputArea>
        <FindAccountAnnouncement
          buttonText=""
          leftText=" / ? "
          onButtonClick={handleFindAccountClick}
          rightText=" !"
        />
        {canSubmit && (
          <Button
            onButtonClick={handleSubmitClick}
            text="   "
            theme={ButtonTheme.DARK}
          />
        )}
      </ContentContainer>
    </ScrollableContainer>
  );
};

export default LoginContainer;
